.note-add-btn{
    width:10vw;
    height:10vw;
    line-height: 10vw;
    border-radius: 50%;
    font-size: 1.2em;
    color:#fff;
    background:#2277BF;
    text-align: center;
    cursor: pointer;
    position: fixed;
    bottom: 5vw;
    right:10vw;
    z-index:99;
}
.rotate-add-btn{
    animation: rotateAddBtn .2s forwards;
}
@keyframes rotateAddBtn {
0{
        transform: rotate(0);
}
70%{
        transform: rotate(150deg);
}
100%{
        transform: rotate(135deg);
}
}
.rerotate-add-btn{
    animation: rerotateAddBtn .2s forwards;
}
@keyframes rerotateAddBtn {
0{
        transform: rotate(135deg);
}
70%{
        transform: rotate(-15deg);
}
100%{
        transform: rotate(0);
}
}
@media screen and (min-width: 1024px) {
.note-add{
        right:25vw;
}
.note-add-btn{
        width:5vw;
        height:5vw;
        line-height: 5vw;
}
}
.note-add-menu-wrapper{
    width:100vw;
    height:100vh;
    background:#f3f3f3;
    position: fixed;
    left:0;
    top:100vh;
}
/*.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}*/
.note-add-menu{
    width:50vw;
    position: absolute;
    bottom:15vw;
    right:10vw;
}
.note-add-menu-li{
    width:100%;
    height:8vh;
    text-align: right;
}
.note-add-menu-content{
    display: inline-block;
    padding:5px 10px;
    background: #fff;
    border-radius: 3px;
}
.show-menu{
    animation: show-menu .5s forwards;
}
@keyframes show-menu {
0%{
        display: none;
        top:100vh;
        height:110vh;
}
70%{
        display: block;
        top:-10vh;
}
100%{
        display: block;
        top:0;
        height:100vh;
}
}
.hide-menu{
    animation: hide-menu .3s forwards;
}
@keyframes hide-menu {
0%{
        display: block;
        top:0;
}
100%{
        display: none!important;
        top:100vh;
}
}
.icon-circle{
    display: inline-block;
    width:10vw;
    height:10vw;
    line-height: 10vw;
    margin-left:5vw;
    border-radius: 50%;
    background:#000;
    color:#fff;
    font-size:1.1em;
    text-align: center;
}
.icon-more3{
    background:#38C5AB;
}
.icon-markdown{
    background:#E4618B;
}
.icon-bi{
    background:#E0AC4B;
}
.icon-zhaoxiangji{
    background:#F34848;
}
.icon-detail{
    background:#5C95EA;
}



