
.icon-share{
    float:right;
    transform: rotate(90deg);
}
.note-detail-remind{
    width:100%;
    height:60px;
    line-height: 60px;
    padding-left:20px;
    box-sizing: border-box;
    color:#000;
    background:#fff;
    font-size: 1.4em;
    text-align: center;
}
.fillfixed{
    width:100%;
    height:63px;
}
.note-notelist-content{
    padding:10px 0;
    line-height: 1.5em;
}
.note-notelist-time{
    padding:15px 20px;
    background: #f3f3f3;
    color:#7D7B7B;
}
.note-notelist-note{
    padding:15px 20px 40px 20px;
}
.note-notelist-note:not(:last-child){
    border-bottom: 1px solid #ccc;
}
.note-notelist-note-time, .note-notelist-note-path{
    font-size: .6em;
    color:#2277BF;
    margin-right:10px;
}
.note-notelist-note-content{
    font-size: .8em;
    margin-top:10px;
}



