
.note-header{
    width:100%;
    height:70px;
    padding:20px;
    box-sizing: border-box;
    background:#2277BF;
    color:#fff;
    position: fixed;
    z-index: 100;
}
.icon{
    font-size: 1.2rem;
}
.left-icon{
    margin-right:.6rem;
}
.note-header-title{
    font-size: 1.6rem;
    font-weight: 700px;
    display: inline-block;
    margin:0;
}
.note-header-icons{
    height:30px;
    line-height: 30px;
    display: inline-block;
    position: absolute;
    right:10px;
}
.note-header-icons .iconfont:not(:last-child){
    margin-right:.6rem;
}




