
.note-detail-header{
    width:100%;
    /*height:60px;*/
    padding:20px 15px;
    box-sizing: border-box;
    background:#2277BF;
    color:#fff;
    position: fixed;
    z-index: 100;
}
.note-detail-header-title{
    display: inline-block;
    font-size: 1.4em;
}
.icon-share{
    float:right;
    transform: rotate(90deg);
}
.note-detail-title{
    width:100%;
    padding:15px 20px;
    box-sizing: border-box;
    color:#000;
    background:#ccc;
    font-size: 1.2em;
    position: relative;
    top:63px;
}
.note-detail-content{
    padding:10px 20px;
    line-height: 1.5em;
    position: relative;
    top:63px;
}



